@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "iconfont";
  src: url("fonts/iconfont.eot");
  src: url("fonts/iconfont.eot?#iefix") format("eot"), url("fonts/iconfont.woff2") format("woff2"), url("fonts/iconfont.woff") format("woff"), url("fonts/iconfont.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.icon {
  font-family: "iconfont";
  font-style: normal;
  font-weight: normal;
  text-rendering: auto;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon {
  display: inline-block;
}

.icon-facebook:before {
  content: "";
}

.icon-fb:before {
  content: "";
}

.icon-insta:before {
  content: "";
}

.icon-play:before {
  content: "";
}

.icon-tw:before {
  content: "";
}

.icon-you:before {
  content: "";
}

.icon-you1:before {
  content: "";
}

.icon-you2:before {
  content: "";
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-fb {
  width: 0.52em;
  height: 1em;
  fill: #FFF;
}

.icon-insta {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-play {
  width: 0.81em;
  height: 1em;
  fill: initial;
}

.icon-tw {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-you {
  width: 1.36em;
  height: 1em;
  fill: none;
}

.icon-you1 {
  width: 1.4em;
  height: 1em;
  fill: #DD2C28;
}

.icon-you2 {
  width: 0.83em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-smoothing: antialiased;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
}

a {
  text-decoration: none;
}

.title-lg {
  color: #fff;
  font-size: 60px;
  font-weight: 700;
  text-transform: uppercase;
}

.title-md {
  color: #1b2550;
  font-size: 48px;
  font-weight: 600;
}

.title-sm {
  color: #303342;
  font-size: 36px;
  font-weight: 600;
}

.title-lg, .title-md, .title-sm {
  font-family: 'Barlow', sans-serif;
  font-style: normal;
}

.article {
  color: #7c8092;
  font-size: 16px;
  line-height: 1.95;
}

.l {
  max-width: 1820px;
  padding: 0 10px;
  margin: 0 auto;
}

.l--sm {
  max-width: 1640px;
}

.header {
  padding-top: 50px;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header__left {
  float: left;
}

.header__right {
  float: right;
  font-size: 0;
}

.header__logo {
  font-weight: 700;
  text-transform: uppercase;
  font-family: 'Barlow', sans-serif;
  font-size: 24px;
  line-height: 40px;
}

.header__logo span {
  display: inline-block;
  vertical-align: top;
  background-color: #032c9e;
  padding: 0px 14px;
  margin-right: 10px;
  height: 40px;
}

.header__menu {
  margin-right: 20px;
  display: inline-block;
  vertical-align: top;
}

.header__menu li {
  display: inline-block;
  vertical-align: top;
}

.header__menu li.is-active a:before {
  width: 100%;
}

.header__menu a {
  display: block;
  padding: 0 26px;
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: 700;
  position: relative;
}

.header__menu a:before {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  background: #fff;
  transition: all 0.2s ease;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
}

.header a {
  color: #fff;
}

.socials {
  display: inline-block;
  vertical-align: top;
}

.socials li {
  display: inline-block;
  vertical-align: top;
  margin-right: 30px;
}

.socials li:last-of-type {
  margin-right: 0;
}

.socials svg {
  fill: #fff;
  width: 16px;
  height: 16px;
}

.fscreen {
  background: #0137a0;
  color: #fff;
  position: relative;
  padding-bottom: 290px;
  margin-bottom: 60px;
}

.fscreen__inner {
  text-align: center;
  padding-top: 280px;
}

.fscreen__pre-title {
  font-size: 18px;
}

.fscreen__nav {
  position: absolute;
  z-index: 1;
  right: 70px;
  top: 50%;
  transform: translateY(-50%);
}

.fscreen__nav a {
  color: rgba(255, 255, 255, 0.5);
  font-size: 18px;
  font-family: 'Barlow', sans-serif;
  font-weight: 700;
  line-height: 30px;
  text-transform: uppercase;
  display: block;
  margin-bottom: 45px;
  padding-right: 80px;
  transition: all 0.2s ease;
  position: relative;
  overflow: hidden;
}

.fscreen__nav a:before {
  content: '';
  position: absolute;
  width: 47px;
  height: 2px;
  background: rgba(255, 255, 255, 0.5);
  transition: all 0.2s ease;
  right: 0;
  transform: translateX(23px);
  top: 50%;
  margin-top: -1px;
}

.fscreen__nav a:last-of-type {
  margin-bottom: 0;
}

.fscreen__nav a.is-active, .fscreen__nav a:hover {
  color: white;
}

.fscreen__nav a.is-active:before, .fscreen__nav a:hover:before {
  transform: translateX(0px);
  background: white;
}

.fscreen .title-lg {
  max-width: 1100px;
  margin: 0 auto 144px;
}

.person {
  max-width: 300px;
  margin: 0 auto;
}

.person img {
  max-width: 96px;
  height: auto;
  max-width: 96px;
  margin-bottom: 24px;
}

.person span {
  display: block;
  color: #fff;
  font-family: 'Barlow', sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 2.1;
  text-transform: uppercase;
}

.vsection__inner {
  padding-left: 13.88889%;
}

.vsection__top-text {
  max-width: 800px;
}

.vsection__two-col {
  margin-bottom: 70px;
}

.vsection__two-col:after {
  content: " ";
  display: table;
  clear: both;
}

.vsection__two-col--mb-big {
  margin-bottom: 105px;
}

.vsection__left {
  width: 262px;
  float: left;
}

.vsection__right {
  float: right;
  width: 72.04301%;
}

.vsection__video {
  margin-bottom: 65px;
  height: 262px;
  position: relative;
  background: gray;
}

.vsection__video img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.vsection__video .play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.vsection__video-main {
  height: 580px;
  margin-bottom: 70px;
}

.vsection__video-main iframe, .vsection__video iframe {
  width: 100%;
  height: 100%;
}

.vsection__blockquotes {
  max-width: 1170px;
  margin: 0 auto 100px;
}

.vsection__blockquotes:after {
  content: " ";
  display: table;
  clear: both;
}

.vsection__blockquotes .blockquote {
  max-width: 44.44444%;
}

.vsection__blockquotes .blockquote:nth-of-type(1n) {
  float: left;
}

.vsection__blockquotes .blockquote:nth-of-type(2n) {
  float: right;
}

.vsection .title-md {
  margin-bottom: 40px;
}

.news__date {
  color: #7c8092;
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: 700;
  margin-bottom: 30px;
}

.news .title-sm {
  margin-bottom: 25px;
}

.news .article {
  margin-bottom: 30px;
}

.blockquote {
  max-width: 490px;
  font-family: 'Barlow', sans-serif;
  margin-bottom: 60px;
}

.blockquote blockquote {
  position: relative;
  padding-left: 50px;
  margin-bottom: 40px;
  color: #7c8092;
  font-size: 36px;
  font-style: italic;
}

.blockquote blockquote:before {
  content: '';
  position: absolute;
  left: 0;
  height: 100%;
  background: #33cbcc;
  width: 2px;
  top: 0;
}

.blockquote__inner {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  font-size: 0;
}

.blockquote__inner img {
  max-width: 48px;
  height: auto;
  max-width: 48px;
  margin-right: 30px;
}

.blockquote__inner span {
  color: #1b2550;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
}

.more-link {
  color: #1b2550;
  font-family: 'Barlow', sans-serif;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  position: relative;
  display: inline-block;
  vertical-align: top;
  padding-left: 34px;
}

.more-link:before {
  content: '';
  position: absolute;
  width: 4px;
  height: 18px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #1b2550;
}

.play-btn {
  width: 96px;
  height: 96px;
  border: 2px solid #fff;
  border-radius: 50%;
  appearance: none;
  background: transparent;
  position: relative;
  font-size: 0;
  line-height: 1;
}

.play-btn i {
  position: absolute;
  top: 50%;
  transform: translate(-44%, -50%);
}

.play-btn svg {
  font-size: 50px;
  fill: #fff;
}
